body{
    margin: 0;
}
.index{
    width: 390px; height: 844px;
    background-color: lightblue;
    margin: 0 auto;
    text-align: center;
    overflow: hidden;
    h2{
        margin-top: 100px ;
    }
    .index_btn{
        cursor: pointer;
        width: 80px; height: 80px;
        background-color: azure;
        border-radius: 50%;
        line-height: 80px;
        font-size: 25px;
        margin: 50px auto 20px;
        &:hover{
            background-color: pink;
        }
    }
}
.menu_box{
    width: 90%; padding: 25px 0 80px;
    background-color: white;
    margin: 0 auto;
    border-radius: 20px;
    .menu{
        font-size: 25px;
    }
    
}
.share_txt{
    margin-top: 50px;
    font-size: 14px;
}
button{
    background: none;
    border: none;
}